import React, { Component } from "react";
import Modal from "../Modal/Modal";
import { FC_GetDistricts } from "../../actions/transfer.action";
import { DistrictTypes } from "../../types/TransferTypes";
import Loading from "../Loading/Loading";
import { search } from "../../utils/functions";
import { BsCheckCircleFill } from "react-icons/bs";

interface OpenSelectDistrictProps {
  onSetOpenSelectDistrict: (status: boolean) => void;
  setSelectedDistrict: (district: DistrictTypes) => void;
  selectedDistrict: DistrictTypes | null;
}
interface OpenSelectDistrictState {
  districts: DistrictTypes[] | null;
  loading: boolean;
  searchData: string;
}

export class OpenSelectDistrict extends Component<
  OpenSelectDistrictProps,
  OpenSelectDistrictState
> {
  constructor(props: OpenSelectDistrictProps) {
    super(props);
    this.state = {
      districts: null,
      loading: false,
      searchData: "",
    };
  }
  GetDistricts = () => {
    this.setState({ loading: true });
    FC_GetDistricts(
      (status: boolean, data: DistrictTypes[] | null, msg: string) => {
        if (data !== null) {
          this.setState({
            districts: data,
            loading: false,
          });
          // console.log(data);
        }
        this.setState({ loading: false });
      }
    );
  };
  FilteredData = () => {
    if (this.state.districts === null) {
      return [];
    }
    var response = this.state.districts;
    return search(response, this.state.searchData) as DistrictTypes[];
  };
  componentDidMount(): void {
    this.state.districts === null && this.GetDistricts();
  }
  render() {
    return (
      <div>
        <Modal
          backDrop={true}
          close={() => this.props.onSetOpenSelectDistrict(false)}
          backDropClose={false}
          displayClose={true}
          padding={{
            title: true,
            body: true,
            footer: undefined,
          }}
          title="Choose District"
        >
          <div className="-mt-5">
            <div className="">
              <input
                placeholder="Search by district name"
                type="text"
                className="px-3 py-2 rounded-md bg-gray-100 w-full"
              />
            </div>
            <div className="w-full overflow-x-auto pt-2">
              <table className="min-w-full text-left text-sm">
                <thead>
                  <th className="border p-2 w-2">#</th>
                  <th className="border p-2 w-10 truncate">District Code</th>
                  <th className="border p-2">District Name</th>
                  <th className="border p-2"></th>
                </thead>
                <tbody>
                  {this.state.loading === true ||
                  this.state.districts === null ? (
                    <tr>
                      <td colSpan={4} className="">
                        <div className="w-full flex flex-row items-center justify-center">
                          <Loading />
                        </div>
                      </td>
                    </tr>
                  ) : this.FilteredData().length === 0 ? (
                    <tr>
                      <td
                        colSpan={4}
                        className="text-center text-xl font-bold text-gray-500"
                      >
                        No Response found!
                      </td>
                    </tr>
                  ) : (
                    this.FilteredData().map((item, i) => (
                      <tr
                        onClick={() => this.props.setSelectedDistrict(item)}
                        className={` group ${
                          this.props.selectedDistrict?.district_code ===
                          item.district_code
                            ? "font-bold text-green-600 bg-green-50"
                            : "hover:bg-accent-200 cursor-pointer"
                        }`}
                      >
                        <td className="border p-2">{i + 1}</td>
                        <td className="border p-2">{item.district_code}</td>
                        <td className="border p-2">{item.district_name}</td>
                        <td className="border p-2 w-1">
                          {this.props.selectedDistrict?.district_code ===
                          item.district_code ? (
                            <div className="flex items-center justify-center">
                              <div>
                                <BsCheckCircleFill className="text-3xl text-green-600 animate__animated animate__bounceIn" />
                              </div>
                            </div>
                          ) : (
                            <div className="px-3 py-1 font-bold bg-accent-400 group-hover:bg-accent-900 group-hover:text-white w-max rounded-md">
                              Choose
                            </div>
                          )}
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}
